const windowOpen = (url: string) => {
    const width = 500;
    const height = 400;
    const left = window.screen.width / 2 - width / 2;
    const top = window.screen.height / 2 - height / 2;

    window.open(
        url,
        '',
        `menubar=no,toolbar=no,resizable=yes,scrollbars=yes,width=${width},height=${height},top=${top},left=${left}`
    );
};

export const twitterShare = (url: string) => {
    windowOpen('http://twitter.com/share?url=' + url);
};

export const copyToClipboard = (url: string) => {
    window.navigator.clipboard.writeText(url);
};

export const linkedinShare = (url: string) => {
    windowOpen('https://www.linkedin.com/sharing/share-offsite/?url=' + url);
};

export const facebookShare = (url: string) => {
    windowOpen('https://www.facebook.com/sharer/sharer.php?u=' + url);
};
